html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

body {
  background-color: var(--white);
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  --primary: #003f49;
  --secondary: #4A61DD;
  --black: #1E3349;
  --white: #FFFFFF;
  --light: #f3f4f7;
  --dark: #1e283e;
}

.darkmode {
  --primary: #003f49;
  --secondary: #3C5DD9;
  --black: #FFFFFF;
  --light: #1e283e;
  --white: #1E3349;
  --dark: #F3F4F7;
}

@font-face {
  font-family: "DM Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("/src/fonts/DMSans-Regular.ttf");
}
@font-face {
  font-family: "DM Serif Display";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("/src/fonts/DMSerifDisplay-Regular.ttf");
}
body {
  overflow-x: hidden;
}

.navbar-container {
  font-family: "DM Sans", sans-serif;
  background-color: var(--primary);
  color: #ffffff;
  padding-bottom: 20px;
  font-size: 14px;
  width: 100vw;
}
.navbar-container.individual-listings {
  position: fixed;
  z-index: 1000;
}
.navbar-container .navbar-top {
  display: flex;
  flex-direction: column;
}
.navbar-container .navbar-top-nav {
  display: flex;
  justify-content: space-between;
  padding: 8px 30px;
}
.navbar-container .navbar-top-nav-list {
  display: flex;
  align-items: center;
  gap: 15px;
}
.navbar-container .navbar-top-nav-list-item-link {
  color: #ffffff;
  text-decoration: none;
  transition: all 350ms ease;
}
.navbar-container .navbar-top-nav-list-item-link:hover {
  opacity: 0.8;
}
.navbar-container .navbar-top-nav-button {
  background-color: none;
  border-radius: 5px;
  border: 0.1px solid #000000;
  background-color: var(--light);
  color: var(--dark);
  padding: 7px 15px;
  cursor: pointer;
  transition: all 350ms ease;
  font-family: "DM Sans", sans-serif;
}
.navbar-container .navbar-top-nav-button:hover {
  box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.3);
}
.navbar-container .navbar-bottom-nav {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.navbar-container .navbar-bottom-nav-logo-container-logo {
  font-family: "DM Serif Display", serif;
  font-size: 24px;
  cursor: default;
}
.navbar-container .navbar-bottom-nav-language {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  cursor: pointer;
  position: relative;
  transition: all 350ms ease;
}
.navbar-container .navbar-bottom-nav-language img {
  height: 16px;
  filter: invert(1);
}
.navbar-container .navbar-bottom-nav-language-dropdown {
  position: absolute;
  top: 30px;
  left: 0px;
  background: var(--white);
  color: var(--black);
  padding: 20px 15px;
  width: 100%;
  z-index: 5000 !important;
  border-radius: 5px;
  flex-direction: column;
  gap: 10px;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.3);
  display: none;
}
.navbar-container .navbar-bottom-nav-language-dropdown.active {
  display: flex;
}
.navbar-container .navbar-bottom-nav-language-dropdown-item-link {
  color: var(--black);
  text-decoration: none;
  transition: all 350ms ease;
  cursor: pointer;
}
.navbar-container .navbar-bottom-nav-search-box {
  width: 500px;
  display: flex;
  align-items: center;
  background-color: var(--white);
  border: none;
  position: relative;
  border-radius: 10px;
}
.navbar-container .navbar-bottom-nav-search-box input {
  width: 100%;
  padding: 10px;
  outline: none;
  background: none;
  color: var(--black);
  font-family: "DM Sans", sans-serif;
  box-shadow: none;
  border: none;
}
.navbar-container .navbar-bottom-nav-search-box img {
  display: flex;
  justify-content: center;
  align-items: center;
  background: none;
  height: auto;
  width: 20px;
  margin: 5px;
  padding: 6px;
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, 0.3);
  border-radius: 50%;
}
.navbar-container .navbar-bottom-nav-login-signup {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  font-size: 16px;
}
.navbar-container .navbar-bottom-nav-login-signup img {
  filter: invert(1);
}
.navbar-container .navbar-bottom-nav-login-signup .login {
  cursor: pointer;
  transition: all 350ms ease;
  text-decoration: none;
  color: #ffffff;
}
.navbar-container .navbar-bottom-nav-login-signup .login:hover {
  opacity: 0.7;
}
.navbar-container .navbar-bottom-nav-login-signup .signup {
  cursor: pointer;
  transition: all 350ms ease;
  text-decoration: none;
  color: #ffffff;
}
.navbar-container .navbar-bottom-nav-login-signup .signup:hover {
  opacity: 0.7;
}

.navbar-bottom-nav-darkmode {
  cursor: pointer;
}

.footer-container {
  width: 100%;
  height: 100%;
  padding: 50px 0;
  background-color: var(--dark);
  color: var(--white);
  font-family: "DM Sans", sans-serif;
}
.footer-container span.mwl {
  text-align: center;
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer-container .footer-columns {
  width: 80%;
  margin: 0 auto;
  display: flex;
  justify-content: space-evenly;
}
.footer-container .footer-column {
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.footer-container .footer-column-title {
  font-size: 18px;
  font-weight: bold;
}
.footer-container .footer-column-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.footer-container .footer-column-list-item-link {
  text-decoration: none;
  color: var(--white);
  font-size: 14px;
  transition: all 350ms ease;
}
.footer-container .footer-column-list-item-link:hover {
  opacity: 0.7;
}

.sidebar-container {
  height: 100%;
  width: 250px;
  font-family: "DM Sans", sans-serif;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: 5px 10px 15px rgba(0, 0, 0, 0.3);
  padding: 30px;
  position: fixed;
  margin-top: 100px;
  z-index: 2000;
}
.sidebar-container .sidebar-nav {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 1000;
}
.sidebar-container .sidebar-nav-category {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
  padding-bottom: 15px;
  border-bottom: 0.5px solid rgba(0, 0, 0, 0.3);
}
.sidebar-container .sidebar-nav-category img {
  width: 30px;
  height: 30px;
}
.sidebar-container .sidebar-nav-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.sidebar-container .sidebar-nav-list .sidebar-nav-item {
  padding: 15px 0;
  border-bottom: 0.5px solid rgba(0, 0, 0, 0.3);
}
.sidebar-container .sidebar-nav-list .sidebar-nav-item a {
  text-decoration: none;
  color: var(--black);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  transition: all 350ms ease;
}
.sidebar-container .sidebar-nav-list .sidebar-nav-item a:hover {
  opacity: 0.9;
}
.sidebar-container .sidebar-nav-list .sidebar-nav-item a img {
  width: 15px;
  height: 15px;
}

.hero-section {
  width: 100%;
}
.hero-section .slider {
  width: 80%;
  margin: 10px auto;
  height: auto;
  overflow: hidden;
  position: relative;
}
.hero-section .slider-length-specifier {
  position: absolute;
  top: 0;
  right: 0;
  padding: 20px;
  font-size: 16px;
  font-weight: bold;
  font-family: "DM Sans", sans-serif;
  z-index: 1000;
}
.hero-section .slider .prev-next-btn .prev {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  border: none;
  outline: 0;
  background: none;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.2);
  padding: 3px;
  cursor: pointer;
}
.hero-section .slider .prev-next-btn .prev img {
  width: 30px;
  filter: invert(1);
}
.hero-section .slider .prev-next-btn .next {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  border: none;
  outline: 0;
  background: none;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.2);
  padding: 3px;
  cursor: pointer;
}
.hero-section .slider .prev-next-btn .next img {
  width: 30px;
  filter: invert(1);
}
.hero-section .slider .slider-images {
  display: flex;
  transition: all 350ms ease;
  box-shadow: 5px 10px 15px rgba(0, 0, 0, 0.3);
}
.hero-section .slider .slider-images img {
  border-radius: 10px;
  height: auto;
  width: 100%;
}

.ad-display-section {
  display: flex;
  flex-direction: column;
  margin: 40px auto;
  padding: 20px;
  padding-bottom: 40px;
  width: 80%;
  box-shadow: 5px 10px 15px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(0, 0, 0, 0.3);
}
.ad-display-section h1 {
  text-align: center;
  font-size: 32px;
  font-family: "DM Sans", sans-serif;
  font-weight: bold;
  color: var(--black);
}
.ad-display-section .logo-section {
  margin-top: 30px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.ad-display-section .logo-section-logo {
  width: auto;
  height: 80px;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.2);
}

.newest-launched-product {
  margin: 100px auto;
  width: 95%;
  box-shadow: 5px 10px 15px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(0, 0, 0, 0.3);
  position: relative;
}
.newest-launched-product.sidebar-yes {
  left: 250px;
}
.newest-launched-product a.shop-more {
  position: absolute;
  padding: 20px;
  right: 20px;
  top: 20px;
  font-family: "DM Sans", sans-serif;
  float: right;
  color: var(--black);
  text-decoration: none;
}
.newest-launched-product h1 {
  text-align: center;
  font-size: 32px;
  font-family: "DM Sans", sans-serif;
  font-weight: bold;
  padding-top: 20px;
  color: var(--black);
}
.newest-launched-product-cards {
  padding: 20px 5px;
  display: flex;
  align-items: center;
  gap: 5px;
}
.newest-launched-product-cards-card {
  display: flex;
  flex-direction: column;
  padding: 30px 15px;
  gap: 15px;
  font-family: "DM Sans", sans-serif;
  font-size: 14px;
  max-width: 100%;
  width: 100%;
  transition: all 350ms ease;
  border: 1px solid rgba(0, 0, 0, 0);
}
.newest-launched-product-cards-card:hover {
  border: 1px solid rgba(0, 0, 0, 0.2);
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.3);
}
.newest-launched-product-cards-card img {
  width: auto;
  height: 150px;
}
.newest-launched-product-cards-card span, .newest-launched-product-cards-card a {
  white-space: initial;
  text-decoration: none;
  color: var(--black);
}
.newest-launched-product-cards-card span.product-price {
  color: var(--primary);
  font-size: 18px;
}
.newest-launched-product-cards-card .product-discount {
  font-size: 12px;
  opacity: 0.8;
  font-style: strikethrough;
}
.newest-launched-product button {
  border: none;
  outline: none;
  position: absolute;
  top: 50%;
  background: var(--light);
  padding: 5px;
  border-radius: 50%;
  box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.newest-launched-product button img {
  width: 30px;
  height: auto;
}
.newest-launched-product button.prev-btn {
  left: 0;
}
.newest-launched-product button.next-btn {
  right: 0;
}

.loader {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 32px;
  font-weight: bold;
  font-family: "DM Sans", sans-serif;
}
.loader.load-finish {
  opacity: 0;
  z-index: -1;
}

.login-container {
  width: 350px;
  margin: 20px auto;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.3);
  color: var(--black);
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.login-container .cant-login {
  display: flex;
  align-items: center;
  justify-content: center;
}
.login-container .cant-login a {
  text-decoration: none;
  color: var(--black);
  text-align: center;
  padding: 10px;
  font-family: "DM Sans", sans-serif;
  font-size: 12px;
  font-weight: bold;
}
.login-container-heading {
  font-family: "DM Serif Display", sans-serif;
  font-size: 28px;
  font-weight: bold;
  text-align: center;
  padding: 10px;
}
.login-container .inputs {
  display: flex;
  flex-direction: column;
  gap: 10px;
  justify-content: center;
  align-items: center;
}
.login-container .inputs input, .login-container .inputs button {
  width: 80%;
  font-family: "DM Sans", sans-serif;
  border: 1px solid rgba(0, 0, 0, 0.5);
  outline: none;
  padding-left: 10px;
}
.login-container .inputs input {
  height: 40px;
  background: var(--white);
  color: var(--black);
  font-weight: bold;
}
.login-container .inputs button {
  height: 35px;
  background: var(--primary);
  color: #ffffff;
  cursor: pointer;
  text-align: center;
  width: calc(80% + 10px);
  font-weight: bold;
}
.login-container .or-text {
  text-align: center;
  font-family: "DM Sans", sans-serif;
}
.login-container .platforms {
  display: flex;
  flex-direction: column;
  gap: 10px;
  justify-content: center;
  align-items: center;
  padding: 10px 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
  border-top: 1px solid rgba(0, 0, 0, 0.3);
}
.login-container .platforms button {
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  outline: none;
  height: 40px;
  border: 1px solid rgba(0, 0, 0, 0.3);
  gap: 10px;
  color: var(--black);
  font-family: "DM Sans", sans-serif;
  cursor: pointer;
  transition: all 350ms ease;
}
.login-container .platforms button:hover {
  box-shadow: 5px 10px 15px rgba(0, 0, 0, 0.3);
}
.login-container .platforms img {
  width: 20px;
  height: auto;
}

.main-content {
  overflow: hidden;
  display: grid;
  grid-template-areas: "header header" "sidebar content";
  position: relative;
}
.main-content .display-content {
  grid-area: content;
}
.main-content .individual-listings {
  grid-area: header;
}
.main-content .sidebar-container {
  grid-area: sidebar;
}

.display-content {
  position: relative;
  margin-left: 320px;
  margin-top: 100px;
}
.display-content .navigation .prev {
  z-index: 100;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 30px;
  cursor: pointer;
  background-color: var(--white);
  border-radius: 20px;
}
.display-content .navigation .prev img {
  padding: 5px 7px;
  width: 25px;
  height: auto;
}
.display-content .navigation .next {
  z-index: 100;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 900px;
  cursor: pointer;
  box-shadow: 5px 10px 15px rgba(0, 0, 0, 0.2);
  background-color: var(--white);
  border-radius: 20px;
  border: 1px solid rgba(0, 0, 0, 0.2);
}
.display-content .navigation .next img {
  padding: 5px 7px;
  width: 25px;
  height: auto;
}
.display-content .trending-products {
  padding: 0 20px;
}
.display-content .trending-products-top {
  display: flex;
}
.display-content .trending-products-heading {
  font-family: "DM Sans", sans-serif;
  font-size: 32px;
  font-weight: bold;
  padding: 30px;
}
.display-content .trending-products-cards {
  display: flex;
  overflow-x: hidden;
  margin-right: 20px;
}
.display-content .trending-products-cards-card {
  border: 0.2px solid rgba(0, 0, 0, 0.2);
  height: 100%;
  width: 200px;
  display: flex;
  position: relative;
  flex-direction: column;
  gap: 10px;
  padding: 15px 20px;
  transition: all 350ms ease;
  font-family: "DM Sans", sans-serif;
  transition: all 350ms ease;
}
.display-content .trending-products-cards-card:hover {
  box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.3);
}
.display-content .trending-products-cards-card img {
  width: 200px;
  height: auto;
}
.display-content .trending-products-cards-card a {
  text-decoration: none;
  color: var(--primary);
  font-size: 16px;
  cursor: pointer;
}
.display-content .trending-products-cards-card span {
  font-size: 14px;
}

.additional-browse-products {
  margin-left: 340px;
  width: 700px;
}
.additional-browse-products-heading {
  font-family: "DM Sans", sans-serif;
  font-size: 32px;
  font-weight: bold;
  padding: 30px;
}
.additional-browse-products-cards {
  display: flex;
  flex-direction: column;
  gap: 50px;
}
.additional-browse-products-cards-card {
  display: flex;
  border: 0.5px solid rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding: 10px 30px;
  box-shadow: 5px 10px 15px rgba(0, 0, 0, 0.3);
  gap: 20px;
}
.additional-browse-products-cards-card-image img {
  width: 250px;
  height: auto;
}
.additional-browse-products-cards-card-text {
  font-family: "DM Sans", sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 15px;
}
.additional-browse-products-cards-card-text-tag span {
  padding: 5px;
  background-color: var(--light);
  display: inline-block;
  border-radius: 20px;
  font-size: 12px;
  font-weight: bold;
}
.additional-browse-products-cards-card-text-heading {
  font-size: 24px;
  font-weight: bold;
  white-space: initial;
  color: var(--dark);
  text-decoration: none;
}
.additional-browse-products-cards-card-text-original {
  font-size: 12px;
}
.additional-browse-products-cards-card-text-discounted {
  font-size: 20px;
  font-weight: bold;
  color: var(--dark);
}
.additional-browse-products-cards-card-text-offer {
  font-size: 12px;
}
.additional-browse-products-cards-card-text-button {
  background: none;
  outline: none;
  border: none;
  font-family: "DM Sans", sans-serif;
  font-weight: bold;
  color: #ffffff;
  background-color: var(--primary);
  padding: 10px 20px;
  cursor: pointer;
}